.C_Player {
    // border: 1px solid #e9e9e9;
    position: absolute;
    height: 100%;
    width: 100%;
    padding: 0;
    left: 0;
    z-index: 100;
    box-sizing: border-box;
    backdrop-filter: blur(3px);
    // box-shadow: 3px 4px 7px 0px #d8d0d0;
    border-radius: 9px;
    overflow: hidden;

    .player-wrapper {
        margin: auto;
        position: relative;

        .header {
            background: #1f2933d4;
            border-radius: 6px 6px 0 0;
            p {
                color: white;
                display: flex;
                align-items: center;
                justify-content: center;
                padding-top: 0.7rem;
                padding-bottom: 0.4rem;
            }
            .exit {
                position: absolute;
                right: 0;
                top: 0;
            }
        }
        .player-body {
            background: #1f2933d4;
            border-radius: 0 0 6px 6px;
            padding: 0 0.6rem 0.6rem 0.6rem;
            video {
                &:focus {
                    outline: 0;
                }
            }
        }
        .fadex {
            animation-name: anim_fadex;
            animation-duration: 3s;
            animation-fill-mode: forwards;
            animation-timing-function: ease-in-out;
        }
    }
}

@keyframes anim_fadex {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
